<template>
	<div>
	<br>
		<div  v-if="alert" class="alert alert-warning" role="alert">
		  Added!
		</div>


		<form @submit="addMessage">
			<div class="form-group">
				<label for="username">
				Username</label>
				<input v-model="message.username" type="text" class="form-control"
				id="username"
				required/>
			</div>
			<div class="form-group">
				<label for="subject">
				Enter a subject</label>
				<input type="text"
				v-model="message.subject"
				 class="form-control"
				id="subject"
				placeholder="enter a subject"
				required/>
			</div>
			<div class="form-group">
				<label for="message">
				Enter a message</label>
				<textarea 
				v-model="message.message"
				class="form-control" id="message" rows="3" required/>
			</div>
			<div class="form-group">
				<label for="imageURL">
				Image URL</label>
				<input type="url" 
				v-model="message.imageURL"
				class="form-control"
				id="imageURL"
				placeholder="enter a imageURL"
				/>
			</div>
			<button type="submit" class="btn btn-outline-success">submit</button>
		</form>

		<br>
		<br>

		<div class="list-unstyled" v-for="message in messages1" :key="message._id">
			  <li class="media" >
			    <img 
			    v-if="message.imageURL"
			    :src="message.imageURL" class="mr-3" :alt="message.subject">
			    <div class="media-body">
			      <h4 class="mt-0 mb-1">{{message.username}}</h4>
			      <h5 class="mt-0 mb-1">{{message.subject}}</h5>
			      {{message.message}}
			      <br>
			      <small>{{message.created}}</small>
			    </div>
			  </li>
			  <hr>
		 </div>
	</div>
</template>

<script>

const API_URL='https://message-board-9891.herokuapp.com/messages';


export default{
	name:"home",
	data:()=>({
		messages:[],
		messages1:[],
		alert:false,
		message:{
			username:'',
			subject:'',
			message:'',
			imageURL:''
		},
	}),
	mounted:function(){
		fetch(API_URL).then(res=>res.json())
			.then(result=>{
				this.messages=result;
				this.messages1=revertArray(this.messages);
			});


	},
	methods:{
		addMessage:function(e){
			e.preventDefault();
			fetch(API_URL,{
				method:'POST',
				body:JSON.stringify(this.message),
				headers:{
					'content-type':'application/json'
				}
			}).then(response=>response.json())
				.then(result=>{
						fetch(API_URL).then(res=>res.json())
						.then(result=>{
							this.messages=result;
							this.messages1=revertArray(this.messages);
						});
						this.alert=true;
						this.message={};
					});
		}
	}
};


function revertArray(array){
	const n=array.length;
	let array1=[];
	for(let i=0;i<n;i++){
		array1[i]=array[n-(i+1)]
	}
	return array1;
}


	
</script>


<style>

hr{
	border-top:1px solid white;
	border-radius:5px;
}
img{
	width:40%;
	height:40vh;
	border-radius:5px;
}


</style>